// Action constants

const SET_API_KEYS = 'SET_API_KEYS'
const DELETE_API_KEYS = 'DELETE_API_KEYS'

const initialState = {}

// Reducer
export default function reducer (state = initialState, action) {
  switch (action.type) {
    case SET_API_KEYS: {
      const { exchange, key1, key2 } = action.payload
      return {
        ...state,
        [exchange]: { name:exchange, key1, key2 }
      }
    }
    case DELETE_API_KEYS: {
      const { exchange } = action.payload
      const { [exchange]: deleted, ...newState } = state
      return newState
    }

    default:
      return state
  }
}

